@-webkit-keyframes text-shadow {
  0% {
    color: white;
    text-shadow: 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(0, 0, 0, 0); }
  15% {
    text-shadow: -1px 1px 6px rgba(0, 0, 0, 0.2), -3px 3px 16px rgba(0, 0, 0, 0.2), -8px 8px 26px rgba(0, 0, 0, 0.2), -16px 16px 36px rgba(0, 0, 0, 0.2); }
  40% {
    color: white;
    text-shadow: 5px 5px 14px rgba(0, 0, 0, 0.3), 10px 10px 22px rgba(0, 0, 0, 0.3), 20px 20px 40px rgba(0, 0, 0, 0.3), 30px 30px 52px rgba(0, 0, 0, 0.3); }
  55%, 80% {
    color: #041b30;
    text-shadow: 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(0, 0, 0, 0); }
  90% {
    color: white; } }

@keyframes text-shadow {
  0% {
    color: white;
    text-shadow: 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(0, 0, 0, 0); }
  15% {
    text-shadow: -1px 1px 6px rgba(0, 0, 0, 0.2), -3px 3px 16px rgba(0, 0, 0, 0.2), -8px 8px 26px rgba(0, 0, 0, 0.2), -16px 16px 36px rgba(0, 0, 0, 0.2); }
  40% {
    color: white;
    text-shadow: 5px 5px 14px rgba(0, 0, 0, 0.3), 10px 10px 22px rgba(0, 0, 0, 0.3), 20px 20px 40px rgba(0, 0, 0, 0.3), 30px 30px 52px rgba(0, 0, 0, 0.3); }
  55%, 80% {
    color: #041b30;
    text-shadow: 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(0, 0, 0, 0); }
  90% {
    color: white; } }

